export default {
    data() {
        return {
            nickname: null,
        }
    },
    methods: {
        setNickname() {
            this.$http({
                method: 'post',
                url: `${BASE_URL}/lock-account/extra_account_info`,
                headers: {
                    'Content-Type': 'application/json',
                },
                data: {
                    nickname: this.nickname
                }
            }).then(function(result) {
                this.$vux.toast.show({
                    text: '成功'
                });
                this.$router.back();
            }.bind(this)).catch(function(e) {
                var msg = '';
                if (e.response) {
                    const { status, error } = e.response.data;
                    msg = error;
                } else if (e.request) {
                    msg = e.request;
                } else {
                    msg = e.message;
                }
                this.$vux.toast.show({
                    type: 'warn',
                    text: msg
                })
            }.bind(this));
        }
    },
    computed: {
        confirmDisabled: function () {
            if (this.nickname) {
                return false;
            } else {
                return true;
            }
        }
    },
    created() {
        this.$http({
            url: `${BASE_URL}/lock-account/extra_account_info`
        }).then(function(result) {
            const data = result.data;
            if (data.status == 0) {
                const { nickname } = data;
                if (nickname) {
                    this.nickname = nickname;
                } else {
                    const { phone } = window.USER;
                    this.nickname = phone;
                }
            }
        }.bind(this)).catch(function(e) {
            var msg = '';
            if (e.response) {
                const { status, error } = e.response.data;
                msg = error;
            } else if (e.request) {
                msg = e.request;
            } else {
                msg = e.message;
            }
            this.$vux.toast.show({
                type: 'warn',
                text: msg
            })
        }.bind(this));
    }
}



